.chatbox-wrapper {
  .chat {
    list-style: none;
    margin: 0;
    padding: 0;
    height: 600px;
  }

  .chat li {
    margin-bottom: 40px;
    padding-bottom: 5px;
    /* border-bottom: 1px dotted #B3A9A9; */
    margin-top: 10px;
    width: 80%;
  }

  .chat li .chat-body p {
    margin: 0;
    /* color: #777777; */
  }

  .chat-care {
    overflow-y: scroll;
    height: 30%;
  }
  .chat-care .chat-img {
    width: 50px;
    height: 50px;
  }
  .chat-care .img-circle {
    border-radius: 50%;
  }
  .chat-care .chat-img {
    display: inline-block;
  }
  .chat-care .chat-body {
    display: inline-block;
    max-width: 80%;
    background-color: #eee;
    border-radius: 12.5px;
    padding: 15px;
  }
  .chat-care .chat-body strong {
    color: #0169da;
  }

  .chat-care .admin {
    text-align: right;
    float: right;
  }
  .chat-care .admin p {
    text-align: left;
  }
  .chat-care .agent {
    text-align: left;
    float: left;
  }
  .chat-care .left {
    float: left;
  }
  .chat-care .right {
    float: right;
  }

  .clearfix {
    clear: both;
  }

  .card-body {
    height: 75vh;
    overflow: hidden;
    overflow-y: scroll;
  }

  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f5;
  }

  ::-webkit-scrollbar {
    width: 12px;
    background-color: #f5f5f5;
  }

  ::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #555;
  }
}
