.Main-wrapper {
  width: 100%;
  height: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}
.card {
  width: 50%;
  height: 30rem;
  /* background-image: url(../assets/Background-image.jpg); */
  text-align: center;
  border-radius: 8px;
  background-color: white;
  box-shadow: 0px 3px 11px rgb(0 0 0 / 8%);
  background-position: center;
  /* border: 3px solid white; */
  margin: auto;
}
.card-wrapper {
  height: 100%;
  display: flex;
  align-items: center;
  width: 100%;
}
.flex {
  display: flex;
}
.card .heading {
  color: black;
  font-size: 40px;
  font-family: Inter;
  font-weight: 600;
  margin: 10px;
  text-transform: capitalize;
}
.card .fields {
  display: flex;
  justify-content: center;
  margin-top: 25px;
  margin-bottom: 25px;
}
.card .button {
  background-color: transparent;
  color: white;
  font-family: cursive;
  font-weight: 600;
  border: 1px solid white;
  border-radius: 21px;
  width: 107px;
  height: 38px;
  margin: 40px;
}
.width-height {
  height: 100vh;
  width: 100%;
}
